<template>
  <v-container fluid>
    <v-col
      text-xs-center
      wrap
    >
    Firebase Database Test
    </v-col>
    <v-col cols="12">
      <v-row>
        <v-text-field
          v-model="user.userName"
          label="name"
          outlined
        ></v-text-field>
        <v-text-field
          v-model="user.userId"
          label="id"
          outlined
        ></v-text-field>
        <v-text-field
          v-model="user.password"
          type="password"
          label="pw"
          outlined
        ></v-text-field>
      </v-row>
      <v-btn @click="save">회원 추가</v-btn>
      <v-btn @click="updateUser">비밀번호 수정</v-btn>
    </v-col>
    <v-col>
      <v-list dense>
        <v-subheader>Users</v-subheader>
        <v-list-item-group
          v-model="users"
          color="primary"
        >
          <v-list-item
            v-for="(item, i) in getUsers"
            :key="i"
            @click="displayUserInfo(item)"
          >
            <v-list-item-icon>
              <v-icon>account_circle</v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title v-text="item.id"></v-list-item-title>
            </v-list-item-content>

            <v-list-item-icon>
              <v-btn small icon @click="deleteUser(item)">
                <v-icon>delete</v-icon>
              </v-btn>
            </v-list-item-icon>
          </v-list-item>
        </v-list-item-group>
      </v-list>
    </v-col>
  </v-container>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  data: () => ({
    user:{
      userName:'',
      userId:'',
      password:'',
    },
    users:null,
    userItems:[]
  }),
  computed:{
    ...mapGetters(['getUsers']),
    allCheck(){
      let check = Object.keys(this.user).every(input=> this.user[input].length > 0)
      return check
    }
  },
  created(){
    this.$store.dispatch('changeTitle','Firebase database')
    this.$store.dispatch('changeSubject','Firebase')
  },
  mounted(){
  },
  methods:{
    displayUserInfo(user){
      this.user.userId = user.id
      this.user.password = user.pw
    },
    save () {
      if(!this.allCheck) return this.$toastr.error( '입력값이 부족합니다.', '입력값 오류',{timeOut: 1500})
      const sameId = this.getUsers.filter(user => user.id === this.user.userId)
      if(sameId.length > 0) return this.$toastr.error( '동일한 아이디가 존재 합니다.', '동일 아이디 오류',{timeOut: 1500})
      //this.$firebase 는 realtimeDB 사용시 쓰고. @firestore는 firestore 사용시 쓴다.
      // this.$firebase.database().ref(`site/user/${this.user.userName}`).set({
      this.$firebase.database().ref(`site/user`).push({
        id: this.user.userId, pw: this.user.password
      })
      //ref() => 루트 파일
      // this.$firebase.database().ref().child('abcd').set({
      //   title: 'abcd', text: 'tttttt'
      // })
    },
    read () {
      //on() : 계속 변화를 감지해서 읽음.DB내에서 바꿔도 내용을 읽을 수 있다.
      // this.$firebase.database().ref().child('abcd').on('value', (sn) => {
      //   // key value 가져오기
      //   const keyValue = sn.val()

      //   console.log(keyValue)
      // })
      this.$firebase.database().ref('site/user/').on('value', (sn) => {
        // console.log(sn)
        // value 값만 가져오기
        const value = Object.values(sn.val())
        
        // key 값만 가져오기
        const key = Object.keys(sn.val())

        // key value 가져오기
        const keyValue = sn.val()
      })
    },
    //한번만 읽는 것은 함수를 async - await로 바꾸는게 편하단다. 이러면 1회성으로 순차적으로 할 때 편하단다
    async readOne () {
      //once() : 한번만 읽음. DB내에서 바꿔도 내용이 바뀌지 않음.
      const sn = await this.$firebase.database().ref().child('abcd').once('value')
      // console.log(sn.val())
    },
    updateUser(){
      const allUser = this.$firebase.database().ref('site/user/')
      allUser.on('child_added',(data)=>{
        let updateData = { pw: this.user.password }
        let userData = data.val()
        if(this.user.userId === userData.id){
          // console.log(this.user.userId, userData.id)
          allUser.child(data.key).update(updateData)
        }
      })
    },
    deleteUser(user){
      const allUser = this.$firebase.database().ref('site/user/')
      allUser.on('child_added',(data)=>{
        // console.log(data.val(), 'key: ', data.key)
        let userData = data.val()
        if(user.id === userData.id){
          // console.log('key: ', data.key)
          allUser.child(data.key).remove()
        }
      })
      // this.$firebase.database().ref(`site/user`).child(key).remove()
    }
  }
}
</script>

